.App {
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #2c3e50;
}

.App-header {
  max-width: 1280px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin: auto;
}

#belgie {
  height: 20vh;
  width: auto;
  margin-top: 5vh;
  margin-bottom: 0;
}

.flex {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  max-width: 1280px;
  margin-top: 5vh;
  margin-bottom: 10vh;
}

.block {
  display: block;
  text-align: center;
  width: 30vw;
  max-width: 400px;
  margin: 5px;
  padding: 0 5px;
}

h1 {
  margin-top: 0vh;
  font-size: 2em;
  font-weight: 700;
  text-transform: uppercase;
  color: #2c3e50;
  background-color: #ecf0f1;
  padding: 20px 0;
  width: 100%;
}

.block h3 {
  font-size: 2rem;
  margin-bottom: 0;
  text-align: center;
  width: 100%
}

.block h4 {
  margin: 1vh 0 0 0;
}

.red {
  color: #e74c3c;
}

.green {
  color: #27ae60
}

.chartDisplay {
  margin-top: 2vh;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.chartGroot {
  margin: 25px;
  display: block;
}

.chartKlein {
  margin: 25px;
  display: none;
}



@media only screen and (max-width: 800px) {
  h1 {
    margin-top: 5vh;
    margin-bottom: 2vh;
  }


  .block h3 {
    font-size: 23px;
  }

  .block h4 {

  }

  .chartDisplay {
    display: block;
    margin: 0;
  }

  .chart {
    margin-bottom: 4vh;
  }

}

@media only screen and (max-width: 550px) {
    .chartGroot {
      margin-left: 25px;
      margin-right: 25px;
      display: none;
    }

    .chartKlein {
      margin-left: 5px;
      margin-right: 5px;
      display: block;
    }
}
